import React from 'react';

const styles = {

    marginLeft: '50px', 
    marginTop: '50px',
    height: '400px', 
    width: '600px', 
    backgroundColor: 'rgb(242, 248, 253)',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center'
}

const ContainerDecorator = storyFn => <div style={styles}>{ storyFn() }</div>;

export { ContainerDecorator }